"use client";

import { Suspense } from "react";
import { useParams, useSearchParams } from "next/navigation";
import {
  SellerSidebar,
  SellerDashboardHeader,
  AnalyticsTab,
  ServicesTab,
  BookingsTab,
  PayoutsTab,
  SettingsTab,
} from "@/components/seller";

// Default stats - replace with real API data
const defaultStats = {
  totalEarnings: 0,
  pendingPayouts: 0,
  totalBookings: 0,
  activeClients: 0,
};

function WorkspacePageContent() {
  const params = useParams();
  const searchParams = useSearchParams();

  const workspaceId = params.workspaceId as string;
  const activeTab = searchParams.get("tab") || "analytics";

  // Get workspace data - in real app, fetch from API
  const workspace = {
    id: workspaceId,
    name: "Workspace",
    color: "#8B5CF6",
  };

  const renderTabContent = () => {
    switch (activeTab) {
      case "analytics":
        return <AnalyticsTab />;
      case "services":
        return <ServicesTab />;
      case "bookings":
        return <BookingsTab />;
      case "payouts":
        return <PayoutsTab />;
      case "settings":
        return <SettingsTab workspace={workspace} />;
      default:
        return <AnalyticsTab />;
    }
  };

  return (
    <div className="flex min-h-screen bg-background">
      {/* Sidebar */}
      <SellerSidebar workspace={workspace} />

      {/* Main Content */}
      <main className="flex-1 min-w-0">
        <div className="p-6 md:p-8 max-w-6xl">
          {/* Dashboard Header - only show on non-settings tabs */}
          {activeTab !== "settings" && (
            <div className="mb-8">
              <SellerDashboardHeader
                workspace={workspace}
                stats={defaultStats}
              />
            </div>
          )}

          {/* Tab Content */}
          {renderTabContent()}
        </div>
      </main>
    </div>
  );
}

export default function WorkspacePage() {
  return (
    <Suspense
      fallback={
        <div className="flex min-h-screen items-center justify-center">
          <div className="h-8 w-8 border-2 border-primary border-t-transparent rounded-full animate-spin" />
        </div>
      }
    >
      <WorkspacePageContent />
    </Suspense>
  );
}
